  pathRoute: ["/admin/api/", "/"], // åœ¨ paths æ¨¡å¼ä¸­ï¼Œæˆªå–ä¸åŒæ–‡ä»¶çš„è·¯å¾„ï¼ˆé»˜è®¤åˆ†å‰²ä¸ºã€'/admin/api/', '/api/', '/'ã€‘ï¼‰
